import React from 'react';
import '../css/Popcho.css';

function Popcho({ onClose, onSave }) {
    return (
        <div className="modalBackground">
            <div className="modalContainer">
                <div className="modalHeader"> </div>
                <div className="radioGroup">
                    <div className="radioOption">
                        <input type="radio" id="option1" name="options" value="option1"/>
                        <label htmlFor="option1" className="radioLabel"> </label>
                    </div>
                    <div className="radioOption">
                        <input type="radio" id="option2" name="options" value="option2"/>
                        <label htmlFor="option2" className="radioLabel">  </label>
                    </div>
                </div>
                <div className="modalButtons">
                    <button onClick={onClose} className="closeButton"></button>
                    <button onClick={onSave} className="saveButton"></button>
                </div>
            </div>
        </div>
    );
}

export default Popcho;
