import React from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { GoogleOAuthProvider, GoogleLogin } from '@react-oauth/google';
import '../css/LogSignPage.css';
import logoImage from '../logo.png';
import { useAuth } from '../context/AuthContext';

const CLIENT_ID = '13094654813-nqglekemv1iff66rsq4luqftbfgv51hi.apps.googleusercontent.com';

function LogSignPage() {
  const navigate = useNavigate();
  const { login } = useAuth();

  const handleGoogleSuccess = async (credentialResponse) => {
    const { credential } = credentialResponse;

    try {
      const res = await fetch('https://moviely.duckdns.org/api/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ token: credential }),
      });
      const data = await res.json();

      if (data.token) {
        login(data.token, data.user);
        if (data.isNewUser) {
          navigate('/add');
        } else {
          navigate('/movie-select');
        }
      } else {
        alert(' .');
      }
    } catch (error) {
      console.error('Backend redirection failed:', error);
      alert('  .');
    }
  };

  const handleGoogleFailure = (error) => {
    console.error('Google Login Failed:', error);
    alert('Google  .');
  };

  return (
    <div className="logSignPage">
      <Link to="/" className="logSign-logo">
        <img src={logoImage} alt="Logo" />
      </Link>
      <div className="logSignBox">
        <h2>  </h2>
        <p>   <br />MOVIELY     .</p>
        <GoogleLogin
          onSuccess={handleGoogleSuccess}
          onError={handleGoogleFailure}
        />
      </div>
    </div>
  );
}

function App() {
  return (
    <GoogleOAuthProvider clientId={CLIENT_ID}>
      <LogSignPage />
    </GoogleOAuthProvider>
  );
}

export default App;
