import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { Link } from 'react-router-dom';
import MvBanner from './MvBanner';
import '../css/MvsrchPage.css';
import logoImage from '../logo.png';
import watchaLogo from '../watcha.png';
import netflixLogo from '../netflix.png';
import disneyPlusLogo from '../disneyplus.png';
import wavveLogo from '../wavve.png';

function MvsrchPage({ userId }) { // userId props 
  const genreMapping = useMemo(() => ({
    ' ': 'All',
    '': '28',
    '': '12',
    '': '16',
    '': '35',
    '': '80',
    '': '99',
    '': '18',
    '': '10751',
    '': '14',
    '': '36',
    '': '27',
    '': '10402',
    '': '9648',
    '': '10749',
    'SF': '878',
    'TV ': '10770',
    '': '53',
    '': '10752',
    '': '37'
  }), []);

  const platformMapping = useMemo(() => ({
    '': 'All',
    '': 'Netflix',
    '': 'Disney Plus',
    '': 'Watcha',
    '': 'Wavve'
  }), []);

  const platforms = useMemo(() => [
    { name: '', logo: null },
    { name: '', logo: netflixLogo },
    { name: '', logo: disneyPlusLogo },
    { name: '', logo: watchaLogo },
    { name: '', logo: wavveLogo }
  ], []);

  const genres = useMemo(() => Object.keys(genreMapping), [genreMapping]);
  const [selectedGenre, setSelectedGenre] = useState(' ');
  const [selectedPlatform, setSelectedPlatform] = useState('');
  const [movies, setMovies] = useState([]);
  const [filteredMovies, setFilteredMovies] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [showPlatforms, setShowPlatforms] = useState(false);
  const [showGenres, setShowGenres] = useState(false);

  useEffect(() => {
    const fetchMovies = async () => {
      try {
        console.log('Fetching movies...');
        const response = await fetch('http://43.203.39.119:8080/api/movies?size=1000', { mode: 'cors' });
        console.log('API  :', response);

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        console.log('API  :', data);

        if (data && data.content) {
          const processedData = data.content.map(movie => ({
            ...movie,
            flatrate: movie.flatrate ? movie.flatrate.split(', ').map(platform => platform.toLowerCase()) : [],
            genre: movie.genre ? movie.genre.split(', ') : []
          }));
          setMovies(processedData);
          setFilteredMovies(processedData);
          setLoading(false);
          console.log('Processed Data:', processedData);
        } else {
          console.error('Unexpected response format:', data);
          setLoading(false);
        }
      } catch (error) {
        console.error('Error fetching movies:', error);
        setLoading(false);
      }
    };

    fetchMovies();
  }, [userId]);

  const filterMovies = useCallback(() => {
    let filtered = [...movies];

    if (selectedPlatform !== '') {
      const selectedPlatformInEnglish = platformMapping[selectedPlatform].toLowerCase();
      console.log(`Selected Platform in English: ${selectedPlatformInEnglish}`);
      filtered = filtered.filter(movie => {
        console.log(`Movie Platforms: ${movie.flatrate}`);
        return movie.flatrate.includes(selectedPlatformInEnglish);
      });
    }

    if (selectedGenre !== ' ') {
      filtered = filtered.filter(movie => movie.genre.includes(genreMapping[selectedGenre]));
    }

    if (searchTerm) {
      const terms = searchTerm.split(' ').filter(term => term);
      const regex = new RegExp(terms.join('|'), 'i');
      filtered = filtered.filter(movie => regex.test(movie.title));
    }

    return filtered;
  }, [movies, selectedPlatform, selectedGenre, searchTerm, genreMapping, platformMapping]);

  useEffect(() => {
    setFilteredMovies(filterMovies());
  }, [filterMovies]);

  const handleSearchClick = () => {
    setFilteredMovies(filterMovies());
  };

  const handleGenreClick = (genre) => {
    setSelectedGenre(genre);
    setShowGenres(false);
  };

  const handlePlatformClick = (platform) => {
    setSelectedPlatform(platform);
    setShowPlatforms(false);
  };

  let resultText = '';
  if (selectedPlatform !== '') {
    resultText += ` : <span style="color: yellow;">${selectedPlatform}</span>`;
  }
  if (selectedGenre !== ' ') {
    if (resultText) {
      resultText += ', ';
    }
    resultText += ` : <span style="color: yellow;">${selectedGenre}</span>`;
  }
  if (resultText) {
    resultText += '  .';
  }

  return (
    <div className="MvsrchPage">
      <Link to="/" className="logo">
        <img src={logoImage} alt="Logo" />
      </Link>
      <div className="searchContainer">
        <div className="dropdownContainer">
          <button onClick={() => setShowPlatforms(!showPlatforms)} className="platformButton"></button>
          {showPlatforms && (
            <div className="platformDropdown">
              {platforms.map((platform) => (
                <button
                  key={platform.name}
                  onClick={() => handlePlatformClick(platform.name)}
                  className="filter"
                >
                  {platform.logo && <img src={platform.logo} alt={platform.name} className="logoIcon"/>}
                  {platform.name}
                </button>
              ))}
            </div>
          )}
        </div>
        <div className="dropdownContainer">
          <button onClick={() => setShowGenres(!showGenres)} className="genreButton"></button>
          {showGenres && (
            <div className="genreDropdown">
              {genres.map((genre) => (
                <button
                  key={genre}
                  onClick={() => handleGenreClick(genre)}
                  className="filter"
                >
                  {genre}
                </button>
              ))}
            </div>
          )}
        </div>
        <input
          type="text"
          className="searchInput"
          placeholder=""
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          onKeyDown={(e) => e.key === 'Enter' && handleSearchClick()} 
        />
        <button className="searchButton" onClick={handleSearchClick}></button>
      </div>
      <div className="resultText" style={{ marginTop: '20px' }}>
        <span dangerouslySetInnerHTML={{ __html: resultText }} />
      </div>
      <div className="movieResults" style={{ marginTop: '30px' }}>
        {loading ? (
          <div>Loading...</div>
        ) : (
          filteredMovies.length > 0 ? (
            <div className="movieGrid">
              {filteredMovies.map((movie, index) => (
                <MvBanner
                  key={index}
                  title={movie.title}
                  poster={movie.poster_path}
                  flatrate={movie.flatrate.join(', ')}
                  rating={Math.round(movie.vote_average / 2)}
                  movieId={movie.id} //  ID 
                  userId={userId} //  ID 
                />
              ))}
            </div>
          ) : (
            <div className="noMovies">  .</div>
          )
        )}
      </div>
    </div>
  );
}

export default MvsrchPage;
