import React, { useState, useEffect, useMemo } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import MvBanner from './MvBanner';
import '../css/MvchoPage.css';
import logoImage from '../logo.png';
import { useAuth } from '../context/AuthContext'; // AuthContext import

function MvchoPage() {
  const { authToken, user, logout } = useAuth(); // AuthContext authToken, user  logout  
  const navigate = useNavigate();
  const genreMapping = useMemo(() => ({
    ' ': 'All',
    '': '28',
    '': '12',
    '': '16',
    '': '35',
    '': '80',
    '': '99',
    '': '18',
    '': '10751',
    '': '14',
    '': '36',
    '': '27',
    '': '10402',
    '': '9648',
    '': '10749',
    'SF': '878',
    'TV ': '10770',
    '': '53',
    '': '10752',
    '': '37'
  }), []);

  const genres = useMemo(() => Object.keys(genreMapping), [genreMapping]);
  const [selectedGenre, setSelectedGenre] = useState(' ');
  const [movies, setMovies] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchMovies = async () => {
      try {
        console.log('Fetching movies...');
        const response = await fetch('http://43.203.39.119:8080/api/movies?size=1000', { mode: 'cors' });
        console.log('API  :', response);

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        console.log('API  :', data);

        if (data && data.content) {
          const processedData = data.content.map(movie => ({
            ...movie,
            flatrate: movie.flatrate ? movie.flatrate.split(', ') : [],
            genre: movie.genre ? movie.genre.split(', ') : [] //    
          }));
          setMovies(processedData);
          console.log('Processed Data:', processedData); 
        } else {
          console.error('Unexpected response format:', data);
        }

        setLoading(false);
      } catch (error) {
        console.error('Error fetching movies:', error);
        setLoading(false);
      }
    };

    fetchMovies();
  }, []);

  const filteredMovies = selectedGenre === ' ' ? movies : movies.filter(movie =>
    movie.genre.includes(genreMapping[selectedGenre])
  );

  const banners = filteredMovies.map((movie, index) => (
    <MvBanner
      key={index}
      title={movie.title}
      poster={movie.poster_path}
      flatrate={movie.flatrate.join(', ')}
      rating={Math.round(movie.vote_average / 2)}
      movieId={movie.id} //  ID 
      userId={user?.id} //  ID 
    />
  ));

  const handleAuthButtonClick = () => {
    if (authToken) {
      logout();
    } else {
      navigate('/log-sign');
    }
  };

  return (
    <div className="MvchoPage">
      <header className="pageHeader">
        <Link to="/" className="logo">
          <img src={logoImage} alt="Logo" />
        </Link>
        <button className="authButton" onClick={handleAuthButtonClick}>
          {authToken ? '' : ''}
        </button>
      </header>
      <div className="mainText">     .</div>
      <div className="subText">   MOVIELY    .</div>
      <div className="stickyTop">
        <Link to="/recommendations">
          <button className="recommendButton">   &gt;</button>
        </Link>
        <div className="genreButtons">
          {genres.map((genre) => (
            <button
              key={genre}
              className={`genreButton1 ${selectedGenre === genre ? 'active' : ''}`}
              onClick={() => setSelectedGenre(genre)}
            >
              {genre}
            </button>
          ))}
        </div>
      </div>
      <div className="bannerGrid">
        {loading ? <div className="loading"> ...</div> : (banners.length > 0 ? banners : <div className="noMovies">   .</div>)}
      </div>
    </div>
  );
}

export default MvchoPage;
