import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import '../css/AddPage.css';
import logoImage from '../logo.png';

function AddPage() {
  const navigate = useNavigate();
  const [gender, setGender] = useState('');
  const [age, setAge] = useState('');

  const handleAddInfo = async (e) => {
    e.preventDefault();

    if (!age) {
      alert(" .");
      return;
    }

    const addInfoData = {
      gender: gender,
      age: parseInt(age, 10) || 0
    };

    console.log("Add Info Data: ", JSON.stringify(addInfoData));

    try {
      const response = await fetch('http://moviely.duckdns.org/update-info', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(addInfoData),
      });

      if (response.ok) {
        navigate('/movie-select');
      } else {
        const errorData = await response.json();
        console.error('  :', errorData);
        alert('  : ' + JSON.stringify(errorData));
      }
    } catch (error) {
      console.error('Error:', error);
      alert('   .');
    }
  };

  const ageOptions = [];
  for (let i = 15; i <= 100; i++) {
    ageOptions.push(<option key={i} value={i}>{i}</option>);
  }

  return (
    <div className="addPage">
      <Link to="/" className="add-logo">
        <img src={logoImage} alt="Logo" />
      </Link>
      <div className="addBox">
        <h2>  </h2>
        <p>      <br />  .</p>
        <form onSubmit={handleAddInfo}>
          <div className="inputGroup">
            <label htmlFor="gender"></label>
            <select
              id="gender"
              name="gender"
              className="genderSelect"
              value={gender}
              onChange={(e) => setGender(e.target.value)}
              required
            >
              <option value="" disabled> </option>
              <option value="female"></option>
              <option value="male"></option>
            </select>
          </div>
          <div className="inputGroup">
            <label htmlFor="age"></label>
            <select
              id="age"
              name="age"
              className="ageSelect"
              value={age}
              onChange={(e) => setAge(e.target.value)}
              required
            >
              <option value="" disabled> </option>
              {ageOptions}
            </select>
          </div>
          <button type="submit" className="addButton"></button>
        </form>
      </div>
    </div>
  );
}

export default AddPage;
