import React, { useState, useEffect } from 'react';
import '../css/Popcal.css';

const Popcal = ({ isOpen, onClose, onSave, onDelete, initialData }) => {
  const [movieData, setMovieData] = useState({
    watch_date: '',
    movie_title: '',
    movie_content: '',
    user_id: '',
    created: '',
    created_by: ''
  });

  useEffect(() => {
    if (initialData) {
      setMovieData({
        watch_date: initialData.start || '',
        movie_title: initialData.title || '',
        movie_content: initialData.movie_content || '',
        user_id: initialData.user_id || '',
        created: initialData.created || '',
        created_by: initialData.created_by || ''
      });
    }
  }, [initialData]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setMovieData(prevData => ({ ...prevData, [name]: value }));
  };

  const handleSave = () => {
    onSave(movieData);  //   onSave   
    onClose();  //    
  };

  const handleDelete = () => {
    onDelete(initialData.id);  //   ID 
    onClose();  //    
  };

  if (!isOpen) return null;

  return (
    <div className="popcal-container">
      <div className="popcal">
        <h2>  </h2>
        <label htmlFor="watch_date"> </label>
        <input
          id="watch_date"
          type="date"
          name="watch_date"
          value={movieData.watch_date}
          onChange={handleInputChange}
        />
        <label htmlFor="movie_title"> </label>
        <input
          id="movie_title"
          type="text"
          name="movie_title"
          placeholder=" "
          value={movieData.movie_title}
          onChange={handleInputChange}
        />
        <label htmlFor="movie_content"> </label>
        <textarea
          id="movie_content"
          name="movie_content"
          placeholder=" "
          value={movieData.movie_content}
          onChange={handleInputChange}
        />
        <div className="popcal-buttons">
          <button onClick={handleDelete}></button>
          <button onClick={handleSave}></button>
        </div>
      </div>
    </div>
  );
};

export default Popcal;
